import React from 'react'
import Mark from './Mark';
const Marks = (props) => (
    <div className='d-flex flex-column p-3'>
        <Mark name=' :' mark={props.values[0]}/>
        <Mark name=' :' mark={props.values[1]}/>
        <Mark name='  :' mark={props.values[2]}/>
        <Mark name=':' mark={props.values[3]}/>
        <Mark name=':' mark={props.values[4]}/>
    </div>
)
export default Marks


